@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Epilogue:ital,wght@0,100..900;1,100..900&display=swap');

:root {
    --clr-light-blue: rgb(119, 184, 210);
    ;
    --clr-orange: #E18C1D;
}


body {
    background-color: #fff;
    font-family: 'Lato';

}

nav {
    display: flex;
    justify-content: space-between;
    padding: 30px 10vw;
    align-items: center;
}

.links {
    display: flex;

}

.link {
    font-family: 'Lato';
    font-weight: 300;
    font-size: 18px;
    line-height: 24px;
    padding: 0 30px;
}

.link:hover {
    font-weight: bold;
}

@media screen and (max-width: 800px) {
    nav {
        padding: 30px 20px;
        align-items: flex-start;
    }

    .logo {
        font-size: 20px;
    }

    .links {
        flex-direction: column;
    }

    .link {
        padding: 0 15px;
        margin: 5px 0;
        font-size: 14px;
    }
}


.home-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 50px;
    justify-content: center;
    align-items: center;
}


.home-title {
    font-family: "Epilogue", sans-serif;
    font-style: normal;
    font-weight: 800;
    font-size: 40px;
    line-height: 24px;
    color: #313131;
    padding: 45px;
}

.github_logo {
    position: absolute;
    top: 0;
    right: 0;
}






.projects-box {
    display: grid;
    grid-template-columns: repeat(auto-fill, 255px);
    width: 60%;
    grid-gap: 2rem 2rem;
    align-items: center;
    justify-content: center;
}

.home-project {
    background: #FFFFFF;
    box-shadow: 0px 0px 6px 2px rgba(138, 138, 138, 0.1);
    border-radius: 10px;


    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: #c2c2c2;

}

.home-project_link {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.home-project_name {
    padding: 8px 0;
    text-align: center;
}

.home-project_img {
    width: 255px;
    height: auto;
    border-radius: 10px 10px 0 0;
}

.home-project:hover,
.home-project:active {
    transition: all 0.2s linear;
    transform: scale(110%);
    color: #515151;
}








footer {
    background-color: #1A1A1A;
    height: 300px;
    margin-top: 50px;
    color: #fff;
    display: flex;
    justify-content: space-between;
    padding: 30px 10vw;
    align-items: center;
}

.socials {
    display: flex;
    flex-direction: column;
}

.socialIcon {
    margin: 10px;
    width: 30px;
}

@media screen and (max-width: 800px) {

    .home-title {
        padding: 0;
    }

    footer {
        flex-direction: column;
        justify-content: center;
        text-align: center;
        height: max-content;
    }

    footer div,
    ul {
        margin: 40px;
    }

}
